import { apiCall, showToast } from "./utils.js";

interface OcrResponse {
    ocr?: string;
}

export async function loadOCR(targetId: string = "ocr-input"): Promise<void> {
    const input =
        document.getElementById(targetId) ||
        document.getElementById("ocr-preview");
    if (!input) {
        console.error("OCR element not found");
        return;
    }
    try {
        const data = await apiCall<OcrResponse>("/api/ocr");
        const value = data.ocr || "";
        if (input.tagName === "TEXTAREA") {
            (input as HTMLTextAreaElement).value = value;
            if (!value) {
                (input as HTMLTextAreaElement).placeholder = "OCR ÌååÏùºÏù¥ ÏóÜÏäµÎãàÎã§. ÌÖçÏä§Ìä∏Î•º ÏßÅÏ†ë ÏûÖÎ†•ÌïòÏÑ∏Ïöî...";
            }
        } else {
            input.textContent = value || "OCR ÌååÏùºÏù¥ ÏóÜÏäµÎãàÎã§.";
            if (!value) input.style.color = "#999";
        }
    } catch (error: unknown) {
        if (input.tagName === "TEXTAREA") {
            (input as HTMLTextAreaElement).value = "";
            (input as HTMLTextAreaElement).placeholder = "OCR Î°úÎìú Ïã§Ìå®";
        } else {
            input.textContent = "OCR Î°úÎìú Ïã§Ìå®";
        }
    }
}

export async function saveOCR(sourceId: string = "ocr-input", statusId: string = "ocr-save-status"): Promise<void> {
    const input = document.getElementById(sourceId) as HTMLTextAreaElement;
    if (!input) {
        console.error("OCR source not found");
        return;
    }
    const statusEl = document.getElementById(statusId);
    try {
        await apiCall("/api/ocr", "POST", { text: input.value });
        if (statusEl) {
            statusEl.textContent = "‚úÖ Ï†ÄÏû•Îê®";
            statusEl.className = "status-text success";
            setTimeout(() => (statusEl.textContent = ""), 2000);
        }
    } catch (error: unknown) {
        if (statusEl) {
            statusEl.textContent = "‚ùå Ï†ÄÏû• Ïã§Ìå®";
            statusEl.className = "status-text error";
        } else {
            const message =
                error instanceof Error ? error.message : "Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.";
            showToast("OCR Ï†ÄÏû• Ïã§Ìå®: " + message, "error");
        }
    }
}
