const copyTimeouts = new Map<HTMLElement, number>();
const sleep = (ms: number) => new Promise((resolve) => setTimeout(resolve, ms));

export type ToastType = "info" | "success" | "warning" | "error";

export function showToast(message: string, type: ToastType = "info"): void {
    const toast = document.createElement("div");
    toast.className = `toast toast--${type}`;
    toast.textContent = message;
    document.body.appendChild(toast);

    requestAnimationFrame(() => toast.classList.add("toast--show"));
    setTimeout(() => {
        toast.classList.remove("toast--show");
        setTimeout(() => toast.remove(), 300);
    }, 3000);
}

export interface ApiErrorData {
    detail?: string;
    message?: string;
    [key: string]: unknown;
}

export class ApiError extends Error {
    status: number;
    canRetry: boolean;

    constructor(status: number, errorData: ApiErrorData = {}) {
        const messages: Record<number, string> = {
            400: "ÏûòÎ™ªÎêú ÏöîÏ≤≠ÏûÖÎãàÎã§",
            401: "Ïù∏Ï¶ùÏù¥ ÌïÑÏöîÌï©ÎãàÎã§",
            403: "Ï†ëÍ∑º Í∂åÌïúÏù¥ ÏóÜÏäµÎãàÎã§",
            404: "ÏöîÏ≤≠Ìïú Î¶¨ÏÜåÏä§Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§",
            429: "ÏöîÏ≤≠Ïù¥ ÎÑàÎ¨¥ ÎßéÏäµÎãàÎã§. Ïû†Ïãú ÌõÑ Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî",
            500: "ÏÑúÎ≤Ñ Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§",
            502: "Í≤åÏù¥Ìä∏Ïõ®Ïù¥ Ïò§Î•òÏûÖÎãàÎã§",
            503: "ÏÑúÎπÑÏä§Î•º ÏùºÏãúÏ†ÅÏúºÎ°ú ÏÇ¨Ïö©Ìï† Ïàò ÏóÜÏäµÎãàÎã§",
            504: "LLM ÏùëÎãµ ÏãúÍ∞ÑÏù¥ Ï¥àÍ≥ºÎêòÏóàÏäµÎãàÎã§. Ïû†Ïãú ÌõÑ Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî",
        };

        const detail =
            messages[status] ||
            errorData.detail ||
            errorData.message ||
            "ÏöîÏ≤≠ Ïã§Ìå®";
        super(detail);
        this.status = status;
        this.canRetry = [429, 500, 502, 503, 504].includes(status);
    }
}

function handleApiError(error: unknown): void {
    console.error("API Error:", error);
    if (error instanceof ApiError && error.canRetry) {
        showToast(`${error.message} [Îã§Ïãú ÏãúÎèÑ Í∞ÄÎä•]`, "warning");
    } else if (error instanceof Error) {
        showToast(error.message, "error");
    } else {
        showToast("Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.", "error");
    }
}

export function registerGlobalErrorHandlers(): void {
    if (typeof window === "undefined") return;
    window.addEventListener("error", (event: ErrorEvent) => {
        if (event.error) {
            showToast(`Ïò§Î•ò: ${event.error.message}`, "error");
        } else {
            showToast("Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.", "error");
        }
    });
    window.addEventListener("unhandledrejection", (event: PromiseRejectionEvent) => {
        const reason = event.reason;
        if (reason instanceof Error) {
            showToast(`Ïò§Î•ò: ${reason.message}`, "error");
        } else if (typeof reason === "string") {
            showToast(`Ïò§Î•ò: ${reason}`, "error");
        } else {
            showToast("Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.", "error");
        }
    });
}

export async function apiCall<T = unknown>(
    url: string,
    method: string = "GET",
    body?: unknown,
    signal?: AbortSignal
): Promise<T> {
    const options: RequestInit = {
        method,
        headers: { "Content-Type": "application/json" },
        signal,
    };
    if (body) {
        options.body = JSON.stringify(body);
    }
    try {
        const response = await fetch(url, options);
        if (!response.ok) {
            let errorData: ApiErrorData = {};
            try {
                errorData = await response.json();
            } catch {
                // ignore parse error
            }
            throw new ApiError(response.status, errorData);
        }
        return await response.json();
    } catch (error: unknown) {
        if (error instanceof DOMException && error.name === "AbortError") {
            throw new Error("ÏöîÏ≤≠Ïù¥ Ï∑®ÏÜåÎêòÏóàÏäµÎãàÎã§");
        }
        handleApiError(error);
        throw error;
    }
}

export function showLoading(elementId: string): void {
    const el = document.getElementById(elementId);
    if (!el) return;
    el.innerHTML = '<div class="loading"></div> Ï≤òÎ¶¨ Ï§ë...';
}

export function copyToClipboard(text: string, buttonEl: HTMLElement | null = null): void {
    navigator.clipboard
        .writeText(text)
        .then(() => {
            if (buttonEl) {
                if (copyTimeouts.has(buttonEl)) {
                    clearTimeout(copyTimeouts.get(buttonEl)!);
                }
                if (!buttonEl.dataset.originalText) {
                    buttonEl.dataset.originalText = buttonEl.textContent || "";
                }
                buttonEl.textContent = "‚úÖ Î≥µÏÇ¨Îê®";
                buttonEl.classList.add("copied");
                const timeout = setTimeout(() => {
                    buttonEl.textContent = buttonEl.dataset.originalText || "";
                    buttonEl.classList.remove("copied");
                    copyTimeouts.delete(buttonEl);
                }, 1500);
                copyTimeouts.set(buttonEl, timeout);
            } else {
                showToast("ÌÅ¥Î¶ΩÎ≥¥ÎìúÏóê Î≥µÏÇ¨ÎêòÏóàÏäµÎãàÎã§!", "success");
            }
        })
        .catch((err) => showToast("Î≥µÏÇ¨ Ïã§Ìå®: " + err.message, "error"));
}

export interface SSEParseResult {
    events: unknown[];
    remainder: string;
}

// SSE Î≤ÑÌçº ÌååÏÑú (Î∂àÏôÑÏ†Ñ Ï≤≠ÌÅ¨ Ï≤òÎ¶¨)
export function parseSSEBuffer(buffer: string): SSEParseResult {
    const events: unknown[] = [];
    let startIdx = 0;

    while (true) {
        const dataIdx = buffer.indexOf("data:", startIdx);
        if (dataIdx === -1) break;
        const endIdx = buffer.indexOf("\n\n", dataIdx);
        if (endIdx === -1) {
            return { events, remainder: buffer.slice(startIdx) };
        }
        const payload = buffer.slice(dataIdx + 5, endIdx).trim();
        try {
            events.push(JSON.parse(payload));
        } catch (e) {
            console.warn("Failed to parse SSE chunk:", payload, e);
        }
        startIdx = endIdx + 2;
    }
    return { events, remainder: "" };
}

// ÏßÑÌñâÎ•† Ï∂îÏ†ï Ìó¨Ìçº
export function showProgressWithEstimate(mode: "batch" | "single"): () => void {
    const estimatedTime = mode === "batch" ? 90000 : 45000; // ms
    const startTime = Date.now();
    const progressInterval = setInterval(() => {
        const elapsed = Date.now() - startTime;
        const progress = Math.min(95, (elapsed / estimatedTime) * 100);
        const bar = document.querySelector(".progress-fill") as HTMLElement;
        if (bar) bar.style.width = progress + "%";
    }, 200);
    return () => clearInterval(progressInterval);
}

export async function withRetry<T>(
    fn: () => Promise<T>,
    retries: number = 2,
    initialDelayMs: number = 500
): Promise<T> {
    let attempt = 0;
    let delay = initialDelayMs;
    let lastError: unknown;
    while (attempt <= retries) {
        try {
            return await fn();
        } catch (err: unknown) {
            lastError = err;
            const retryable =
                !err ||
                (err instanceof ApiError && err.canRetry) ||
                (typeof err === "object" &&
                    err !== null &&
                    "status" in err &&
                    typeof (err as { status?: number }).status === "number" &&
                    (err as { status: number }).status >= 500);
            if (attempt === retries || !retryable) {
                break;
            }
            await sleep(delay);
            delay *= 2;
            attempt += 1;
        }
    }
    throw lastError;
}
